.swiper{
  position: relative;
  height: 212px;
  // 顶部导航
  .search-box {
  position: absolute;
  top: 25px;
  width: 100%;
  padding: 0 10px;

    // left side white area
    .search {
      height: 34px;
      margin: 0 10px;
      padding: 9px 5px 5px 8px;
      border-radius: 3px;
      background-color: #fff;
      // location
      .location {
        float: left;
        .icon-arrow {
          margin-left: 2px;
          font-size: 12px;
          color: #7f7f80;
        }
      }

      // search form
      .form {
        float: left;
        border-left: solid 1px #e5e5e5;
        margin-left: 12px;
        line-height: 16px;
        .icon-search {
          vertical-align: middle;
          padding: 0 2px 0 12px;
          color: #9c9fa1;
          font-size: 15px;
        }
        .text {
          vertical-align: middle;
          font-size: 14px;
          color: #9c9fa1;
        }
      }
    }

    // right side map icon
    .map-icon{
      padding-top: 5px;
      .icon-map {
        font-size: 25px;
        color: #fff;
      }
    }
  }
  .content {
    height: 212px;
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 48px;
    user-select: none;
  }
  .swiperfailed{
    height: 212px;
    display: flex;
    color: #ffffff;
    justify-content: center;
    align-items: center;
    font-size: 20px;
  }
}
// alble to use nested css
.nav{
  padding: 10px 0;
  .adm-grid-item{
    text-align: center;
  }
  img{
    width: 48px;
  }
  h2{
    color:#32ac79;
    font-size: 13px;
    margin-top: 7px;
    font-weight: normal;
  }
}

// rental group
.group {
  background-color: #f6f5f6;
  overflow: hidden;
  padding: 0 10px;

  .group-title {
    position: relative;
    margin: 15px 0px 15px 10px;
    font-size: 15px;
    .more {
      color: #787d82;
      position: absolute;
      right: 0;
      font-size: 14px;
      font-weight: normal;
    }
  }

  .adm-grid-item .group-item {
    height: 75px;
    background-color: #fff;
    .desc {
      margin-top: 6px;
      float: left;
      padding-left: 20px;
      .title {
        font-weight: bold;
        font-size: 13px;
        margin-bottom: 5px;
      }
      .info {
        font-size: 12px;
        color: #999;
      }
    }
    img {
      margin-top: 13px;
      margin-left: 20px;
      width: 55px;
    }
  }
  .adm-grid{
    margin-bottom: 10px;
  }
}

// news
.news{
  padding: 10px 10px 50px 10px;
  background-color: #fff;
  overflow: hidden;
  .group-title {
    margin: 10px 0 5px 10px;
    font-size: 15px;
  }
  .news-item {
    height: 120px;
    padding: 15px 10px 15px 0;
    border-bottom: 1px solid #e5e5e5;
  }
  .news-content {
    overflow: hidden;
    height: 100%;
    padding-left: 20px;
  }
  .imgwrap {
    float: left;
    height: 90px;
    width: 120px;
  }
  .img {
    height: 90px;
    width: 120px;
    margin-left: 10px;
  }
  .title {
    margin-bottom: 15px;
    font-size: 14px;
  }
  .news-info{
    color: #9c9fa1;
    font-size: 12px;
    .info2{
      float: right;
    }
  }
}
