html, body{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: #333;
  background-color: #fff;
  margin: 0;
  padding: 0;
}

*{
  box-sizing: border-box;
}

h2,
h3,
p,
ul {
  margin: 0;
}

ul {
  list-style: none;
}

html, body, #root, .App{
  height: 100%;
}